import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'lib-link-icon-widget',
  templateUrl: './link-icon-widget.component.html',
  styleUrls: ['./link-icon-widget.component.scss']
})
export class LinkIconWidgetComponent implements OnInit {
  @Input() widget: any;

  constructor(
    private router: Router
  ) { }

  ngOnInit(): void {
  }

  onClick(url) {
    if (url) {
      this.router.navigateByUrl(url);
    }
  }

  public canActivate(filed): boolean {
    return this.widget.config.hasOwnProperty(filed);
  }

}

export const LinkIconWidget = {
  type: 'link',
  component: LinkIconWidgetComponent,
  defaultConfig: []
};
