import { Component, OnInit, Input, AfterViewInit, AfterViewChecked, OnDestroy, ViewChild, ElementRef, Renderer2 } from '@angular/core';
declare var zingchart: any;
// refer: https://www.zingchart.com/gallery/stacked-area-chart-with-crosshair

@Component({
  selector: 'lib-chart-widget',
  templateUrl: './chart-widget.component.html',
  styleUrls: ['./chart-widget.component.scss']
})
export class ChartWidgetComponent implements OnInit, AfterViewInit, AfterViewChecked, OnDestroy {
  @ViewChild('chartContainerRef') el: ElementRef;
  @Input() widget: any;
  @Input() gridInfo: any;

  public graphConfig: any;
  private id: string;

  constructor(
    private renderer2: Renderer2
  ) { }

  ngOnInit(): void {
    this.initConfig();
    // Key is unique
    this.id = `graph-${new Date().getTime()}`;
  }

  ngAfterViewInit() {
    // Use Angular's Renderer2 to create the div element
    const divContainer = this.renderer2.createElement('div');
    // Set the id of the div
    this.renderer2.setProperty(divContainer, 'id', this.id);
    // Add class
    this.renderer2.addClass(divContainer, 'h-100');
    this.renderer2.addClass(divContainer, 'w-100');
    // Append the created div to the body element
    this.renderer2.appendChild(this.el.nativeElement, divContainer);
    // Render
    setTimeout(() => {
      zingchart.render({
        id: this.id,
        data: this.graphConfig,
        height: '100%',
        width: '100%'
      });
    }, 0);
  }

  ngAfterViewChecked() {
    // Remove "Power by zingchart"
    const loop = setInterval(() => {
      const els = document.querySelectorAll('[id*="license-text"]');
      if (els.length > 0) {
        els.forEach(e => {
          e.innerHTML = '';
        });
      }
    }, 1000);
    // After 3 seconds stop
    setTimeout(() => clearInterval(loop), 3000);
  }

  ngOnDestroy() {
    zingchart.exec(this.id, 'destroy');
  }

  initConfig() {
    switch (this.widget.config.active) {
      case 'line':
        this.graphConfig = this.lineJSON(this.widget);
        break;
      case 'area':
        this.graphConfig = this.areaJSON(this.widget);
        break;
      case 'bar':
        this.graphConfig = this.barJSON(this.widget);
        break;
      case 'hbar':
        this.graphConfig = this.barJSON(this.widget);
        break;
      case 'bar3d':
        this.graphConfig = this.barJSON(this.widget);
        break;
      case 'pie':
        this.graphConfig = this.pieJSON(this.widget);
        break;
      default:
        break;
    }
  }

  lineJSON(widget) {
    const xAxis = widget.config.chart.option.xAxis;
    const yAxis = widget.config.chart.option.yAxis;
    const valueScaleX = widget.config.chart.option.valueScaleX;
    const valueScaleY = widget.config.chart.option.valueScaleY;
    const defaulColor = widget.config.chart.option.defaulColor;
    const series = [];
    widget.config.chart.series.forEach((el, i) => {
      series.push({
        values: el.values,
        lineWidth: '1px',
        lineColor: i > 0 ? el.lineColor : defaulColor,
        marker: {
          visible: false
        }
      });
    });
    return {
      type: this.widget.config.active,
      theme: 'classic',
      backgroundColor: 'transparent', // bg black
      plot: {
        legend: {
          visible: false, // hidden
        }
      },
      plotarea: {
        'adjust-layout': true, // align-center
      },
      scaleX: {
        values: valueScaleX ? valueScaleX : null,
        label: {
          text: xAxis ? xAxis : xAxis,
          fontColor: '#959393',
          fontSize: 12,
          fontFamily: 'SF Pro Text-Regular',
        },
        guide: {
          lineColor: '#808080',
          lineStyle: 'solid'
        },
        item: {
          fontColor: '#808080',
          fontSize: '12px',
          fontWeight: 'normal',
          offsetY: '5%'
        },
        lineColor: '#808080',
        lineStyle: 'solid',
        lineWidth: '1px',
        tick: {
          lineColor: '#808080',
          lineWidth: '1px'
        }
      },
      scaleY: {
        values: valueScaleY ? valueScaleY : null,
        label: {
          text: yAxis ? yAxis : null,
          fontColor: '#959393',
          fontSize: 12,
          fontFamily: 'SF Pro Text-Regular',
        },
        guide: {
          lineColor: '#808080',
          lineStyle: 'solid'
        },
        item: {
          fontColor: '#808080',
          fontFamily: 'arial',
          fontSize: '12px',
          fontWeight: 'normal',
          offsetX: '-5%'
        },
        lineColor: '#808080',
        lineWidth: '1px',
        tick: {
          lineColor: '#808080',
          lineWidth: '1px'
        }
      },
      series: series // []
    };
  }

  areaJSON(widget) {
    const xAxis = widget.config.chart.option.xAxis;
    const yAxis = widget.config.chart.option.yAxis;
    const valueScaleX = widget.config.chart.option.valueScaleX;
    const valueScaleY = widget.config.chart.option.valueScaleY;
    const defaulColor = widget.config.chart.option.defaulColor;
    const series = [];
    widget.config.chart.series.forEach((el, i) => {
      series.push({
        values: el.values,
        backgroundColor: i > 0 ? el.backgroundColor : defaulColor,
        lineWidth: '0px',
        marker: {
          visible: false
        }
      });
    });
    return {
      type: this.widget.config.active,
      theme: 'classic',
      backgroundColor: 'transparent', // bg black
      plot: {
        legend: {
          visible: false, // hidden
        }
      },
      plotarea: {
        'adjust-layout': true, // align-center
      },
      scaleX: {
        values: valueScaleX ? valueScaleX : null,
        label: {
          text: xAxis ? xAxis : null,
          fontColor: '#959393',
          fontSize: 12,
          fontFamily: 'SF Pro Text-Regular',
        },
        guide: {
          lineColor: '#808080',
          lineStyle: 'solid'
        },
        item: {
          fontColor: '#808080',
          fontSize: '12px',
          fontWeight: 'normal',
          offsetY: '5%'
        },
        lineColor: '#808080',
        lineStyle: 'solid',
        lineWidth: '1px',
        tick: {
          lineColor: '#808080',
          lineWidth: '1px'
        }
      },
      scaleY: {
        values: valueScaleY ? valueScaleY : null,
        label: {
          text: yAxis ? yAxis : null,
          fontColor: '#959393',
          fontSize: 12,
          fontFamily: 'SF Pro Text-Regular',
        },
        guide: {
          lineColor: '#808080',
          lineStyle: 'solid'
        },
        item: {
          fontColor: '#808080',
          fontFamily: 'arial',
          fontSize: '12px',
          fontWeight: 'normal',
          offsetX: '-5%'
        },
        lineColor: '#808080',
        lineWidth: '1px',
        tick: {
          lineColor: '#808080',
          lineWidth: '1px'
        }
      },
      series: series // []
    };
  }

  barJSON(widget) {
    const stack = widget.config.chart.option.stack;
    const xAxis = widget.config.chart.option.xAxis;
    const yAxis = widget.config.chart.option.yAxis;
    const valueScaleX = widget.config.chart.option.valueScaleX;
    const valueScaleY = widget.config.chart.option.valueScaleY;
    const defaulColor = widget.config.chart.option.defaulColor;
    const series = [];
    widget.config.chart.series.forEach((el, i) => {
      series.push({
        values: el.values,
        backgroundColor: i > 0 ? el.backgroundColor : defaulColor,
        lineWidth: '0px',
        marker: {
          visible: false
        }
      });
    });
    return {
      type: this.widget.config.active,
      theme: 'classic',
      backgroundColor: 'transparent', // bg black
      plot: {
        legend: {
          visible: false, // hidden
        },
        stacked: stack ? true : false,
        'stack-type': stack > 0 ? `${stack}%` : `normal` // Refer: https://www.zingchart.com/docs/chart-types/bar
      },
      plotarea: {
        'adjust-layout': true, // align-center
      },
      scaleX: {
        values: valueScaleX ? valueScaleX : null,
        label: {
          text: xAxis ? xAxis : null,
          fontColor: '#959393',
          fontSize: 12,
          fontFamily: 'SF Pro Text-Regular',
        },
        guide: {
          lineColor: '#808080',
          lineStyle: 'solid'
        },
        item: {
          fontColor: '#808080',
          fontSize: '12px',
          fontWeight: 'normal',
          offsetY: '5%'
        },
        lineColor: '#808080',
        lineStyle: 'solid',
        lineWidth: '1px',
        tick: {
          lineColor: '#808080',
          lineWidth: '1px'
        }
      },
      scaleY: {
        values: valueScaleY ? valueScaleY : null,
        label: {
          text: yAxis ? yAxis : null,
          fontColor: '#959393',
          fontSize: 12,
          fontFamily: 'SF Pro Text-Regular',
        },
        guide: {
          lineColor: '#808080',
          lineStyle: 'solid'
        },
        item: {
          fontColor: '#808080',
          fontFamily: 'arial',
          fontSize: '12px',
          fontWeight: 'normal',
          offsetX: '-5%'
        },
        lineColor: '#808080',
        lineWidth: '1px',
        tick: {
          lineColor: '#808080',
          lineWidth: '1px'
        }
      },
      series: series // []
    };
  }

  pieJSON(widget) {
    return {
      type: 'pie',
      backgroundColor: 'transparent', // bg black
      plotarea: {
        'adjust-layout': true, // align-center
      },
      series: widget.config.pie.series // []
    };
  }
}

export const ChartWidget = {
  name: 'Chart',
  type: 'component',
  component: ChartWidgetComponent,
  // TYPE: area, line, bar, hbar, bar3d, pie
  defaultConfig: [
    {
      active: 'line',  // TYPE: area, line, bar, hbar, bar3d, pie
      chart: {
          option: {
              stack: 0,
              yAxis: null,
              // defaulColor: '#9FFFB1',
          },
          series: [
              {
                  values: [1, 4, 1, 2, 1] ,
                  lineColor: '#64b5f6',
                  backgroundColor: '#64b5f6',
                  text: 'Data 1',
              },
              {
                  values: [5, 1, 5, 3, 4] ,
                  lineColor: '#F7B50E',
                  backgroundColor: '#F7B50E',
                  text: 'Data 1',
              }
          ]
      },
      pie: {
          option: { 
              // defaulColor: '#9FFFB1',
          },
          series: [
              {
                  values: [23],
                  backgroundColor: '#ff4081',
                  text: 'Day 1',
              },
              {
                  values: [20],
                  backgroundColor: '#e040fb',
                  text: 'Day 2'
              },
              {
                  values: [25],
                  backgroundColor: '#448aff',
                  text: 'Day 3'
              },
              {
                  values: [25],
                  backgroundColor: '#ff5252',
                  text: 'Day 4'
              },
              {
                  values: [25],
                  backgroundColor: '#ff6e40',
                  text: 'Day 5'
              },
              {
                  values: [25],
                  backgroundColor: '#69f0ae',
                  text: 'Day 6'
              },
              {
                  values: [25],
                  backgroundColor: '#7c4dff',
                  text: 'Day 7'
              }
          ]
      }
    }
  ]
};
