import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PrivateGuard } from './services/private-guard';
import { PublicGuard } from './services/public-guard';

import { PrivatePagesComponent } from './components/private-pages/private-pages.component';
import { PublicPagesComponent } from './components/public-pages/public-pages.component';

const routes: Routes = [
  {
    path: 'private',
    component: PrivatePagesComponent,
    canActivate: [PrivateGuard],
  },
  {
    path: 'public',
    component: PublicPagesComponent,
    canActivate: [PublicGuard],
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
