import { Component, OnInit, ViewChild } from '@angular/core';
import { BsModalRef, BsModalService, ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'lib-modal-select-sensor',
  templateUrl: './modal-select-sensor.component.html',
  styleUrls: ['./modal-select-sensor.component.scss']
})
export class ModalSelectSensorComponent implements OnInit {
  @ViewChild('template', { static: false }) template: ModalDirective;

  public modalRef: BsModalRef;

  public sensors: any[] = [
    {
      id: 1,
      icon: 'feather-grid',
      active: false,
      value: '12.334.344,755',
      variable: 'GPS'
    },
    {
      id: 2,
      icon: 'feather-grid',
      active: true,
      value: '75.5',
      variable: 'Temprature'
    },
    {
      id: 3,
      icon: 'feather-grid',
      active: false,
      value: '47.65',
      variable: 'Huminity'
    },
    {
      id: 4,
      icon: 'feather-grid',
      active: false,
      value: '97%',
      variable: 'Battery'
    },
    {
      id: 5,
      icon: 'feather-grid',
      active: false,
      value: '58',
      variable: 'Gauge'
    },
    {
      id: 6,
      icon: 'feather-grid',
      active: false,
      value: '-59',
      variable: 'RSSI_NB'
    },
    {
      id: 7,
      icon: 'feather-grid',
      active: false,
      value: '0',
      variable: 'RSSI_WIFI'
    },
    {
      id: 8,
      icon: 'feather-grid',
      active: false,
      value: '1',
      variable: 'Lamp'
    },
    {
      id: 9,
      icon: 'feather-grid',
      active: false,
      value: '47.65',
      variable: 'Huminity'
    }
  ];

  public selected: any[] = [this.sensors[2]];

  constructor(
    private modalService: BsModalService
  ) { }

  ngOnInit(): void {
  }

  showModal(): void {
    this.modalRef = this.modalService.show(this.template, { class: 'modal-lg modal_select_sensor' });
  }

  hideModal(): void {
    this.modalRef.hide();
  }

  select(row): void {
    this.selected.push(row);
  }

  unSelect(row): void {
    this.selected = this.selected.filter(f => f.id !== row.id);
  }
}
