import {
  Component,
  OnInit,
  ViewChild
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalSelectSensorComponent } from './modal-select-sensor/modal-select-sensor.component';

import ExampleData from './../../../../../vms-site/src/app/services/example';
import ExampleTheme from './../../../../../vms-site/src/app/services/themes';

@Component({
  selector: 'lib-widget-setting',
  templateUrl: './widget-setting.component.html',
  styleUrls: ['./widget-setting.component.scss']
})
export class WidgetSettingComponent implements OnInit {
  @ViewChild('modalSelectSensor', { static: false }) modalSelectSensor: ModalSelectSensorComponent;

  public dataWidget: any;

  public graphsColor: any[] = [
    {
      id: 1,
      color: '#97C9DE',
      value: '{Graph 1}'
    },
    {
      id: 2,
      color: '#9DCB7A',
      value: '{Graph 2}'
    }
  ];

  public pinsColor: any[] = [
    {
      id: 1,
      color: '#97C9DE',
      value: '{Pin 1}',
      active: true
    },
    {
      id: 2,
      color: '#C58ECC',
      value: '{Pin 2}'
    },
    {
      id: 3,
      color: '#EA857A',
      value: '{Pin 3}'
    },
    {
      id: 4,
      color: '#73B18D',
      value: '{Pin 4}'
    },
    {
      id: 5,
      color: '#73B14D',
      value: '{Pin 5}'
    }
  ];

  public initialZoomActive: number = 10;
  public screenParameterId: string;
  public screenParameterName: string;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {
    this.activatedRoute.queryParams.subscribe(params => {
      this.screenParameterId = params['id'];
      this.screenParameterName = params['previous'] ? params['previous'] : 'dashboard';
    });
  }

  ngOnInit(): void {
    this.loadDataWidget();
  }

  openModal() {
    this.modalSelectSensor.showModal();
  }

  loadDataWidget() {
    this.dataWidget = ExampleData[this.screenParameterName]['widgets'].find(f => f.id == this.screenParameterId);
    // Get theme string
    const currentTheme = localStorage.getItem('theme') ? localStorage.getItem('theme').replace(/"/g, '') : 'dark.default';
    // Get type witget
    const widgetType = this.dataWidget.widget['type'];

    if (this.dataWidget.widget['type'] === 'ChartWidget') {
      const defaulColor = ExampleTheme[currentTheme][widgetType]['chart']['option']['defaulColor'];
      // Merge defaulColor theme
      this.dataWidget.widget['config']['chart']['option']['defaulColor'] = defaulColor;
    } else {
      const themColors = ExampleTheme[currentTheme][widgetType];
      // Merge defaulColor theme
      this.dataWidget.widget['config'] = {...this.dataWidget.widget['config'], ...themColors};
    }
  }

  onClickWidget() {
    this.router.navigateByUrl(`/private?page=${this.screenParameterName}`);
  }

  // Check show button
  public canActivate(field): boolean {
    return this.dataWidget && this.dataWidget.widget.config.hasOwnProperty(field);
  }
}

export const WidgetSettingWidget = {
  type: 'page',
  component: WidgetSettingComponent,
  defaultconfig: [],
};
